/*.header{width: 100vw;}*/

.container{ position: relative;
            width: 100vw;
  &__pic {width: 100%; 
          height: 100%;
        min-height: 568px;
      min-width: 350px;} 
    &__menu {width: 44px;
      height: 44px;
      background-color:#0271bc;
           position: fixed;
           top:0px;
           left: 0px;
           text-align:center;
           &--icon{ width: 16px;
          height: 16px;
        padding:15px, 14px;
      margin-top: 15px;}}
      &__text{ position: absolute;
               top:50%;
               left:50%;
               transform: translate(-50%);
               text-align: center;
              &--main, 
              &--second {color:$color-headFooter;
                      font-family:$font-main;}
              &--main {text-transform: uppercase;
                      width: 290px;
                      height: 110px;
                      font-size: 42px;
                      width:8em}
              &--second {font-size: 16px}
                      
              }
              &__button { width: 46px;
                          height: 46px;
                          border-radius: 50%;
                          position: relative;
                          display: inline-block;
                          transform: translate(250px,-100px);
                      
                        }
   }
